import OwlCarousel from 'react-owl-carousel';
import 'owl.carousel/dist/assets/owl.carousel.css';
import 'owl.carousel/dist/assets/owl.theme.default.css';
import img1 from "../images/py.png" 
import img2 from "../images/c_c++.avif"
import img3 from "../images/java.webp"
import img4 from "../images/sbit_logo.png"
import img5 from "../images/ui_ux.jpg"
import img6 from "../images/internship.jpg"
import img7 from "../images/react.jpg"
import img8 from "../images/training.webp"

export default function Owl_Carousel(){
    const options = {
        loop: true,
        margin: 10,
        autoplayTimeOut: 4000,
        nav: true,
        autoplay: true,
        responsive: {
            0: {
                items: 1
            },
            600: {
                items: 3
            },
            1000: {
                items: 4
            }
        }
    }
    return(
        <>
            <div style={{
                    height: "300px"
                }} className=" mt-4 row ">
                    <OwlCarousel {...options} className='owl-theme' loop margin={10} nav>
                        <div class='item' >
                            <img src={img1} className='owl'></img>
                            <h4 className='text-center w-100'>PYTHON</h4>
                        </div>

                        <div class='item'>
                            <img src={img3} className='owl'></img>
                            <h4 className='text-center w-100'>JAVA</h4>
                        </div>
                        <div class='item'>
                            <img src={img2} className='owl'></img>
                            <h4 className='text-center w-100'>C,C++</h4>
                        </div>
                        <div class='item'>
                            <img src={img4} className='owl'></img>
                            <h4 className='text-center w-100'>SBIT</h4>
                        </div>
                        <div class='item'>
                            <img src={img5} className='owl'></img>
                            <h4 className='text-center w-100'>UI UX DESIGNER</h4>
                        </div>
                        <div class='item'>
                            <img src={img6} className='owl'></img>
                            <h4 className='text-center w-100'>SPRING BOOT</h4>
                        </div>
                        <div class='item'>
                            <img src={img7} className='owl'></img>
                            <h4 className='text-center w-100'>TRAINING</h4>
                        </div>
                        <div class='item'>
                            <img src={img8} className='owl'></img>
                            <h4 className='text-center w-100'>TRAINING</h4>
                        </div>



                    </OwlCarousel>

                </div>
        </>
    )
}