import { Link } from "react-router-dom"
import img4 from "../images/sbit_logo.png" 

export default function SbitHeader() {
    return (
        <div className="Header">
            {/* Header */}
            <div className="text-center header w-100 pb-3">
                <span className="sbit">SBIT COMPUTER EDUCATION</span>
            </div>
            {/* Navbar */}
            <nav className="navbar navbar-expand-lg ">
                <div className="container-fluid">
                    <img src={img4} className="logo_img"></img>
                    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                        <span className="navbar-toggler-icon"></span>
                    </button>
                    <div className="collapse navbar-collapse" id="navbarSupportedContent">
                        <ul className="navbar-nav navigation me-auto mb-2 mb-lg-0">
                            <li className="nav-item  px-3 py-1">

                                <Link className="nav-link" to="/">
                                    Home</Link>
                            </li>
                            <li className="nav-item px-3 py-1">
                                <Link to="/About" className="nav-link" href="#">About</Link>
                            </li>
                            <li className="nav-item px-3 py-1">
                                <Link to="/course" className="nav-link">course</Link>
                            </li>
                            <li className="nav-item px-3 py-1">
                                <Link to="/project" className="nav-link">Project</Link>
                            </li>
                            <li className="nav-item px-3 py-1">

                                <Link to="/service" className="nav-link">Services</Link>
                            </li>
                            <li className="nav-item px-3 py-1">
                                <Link to="Internship" className="nav-link">Internship</Link>
                            </li>
                        </ul>
                        <div class="d-flex">
                            <Link to="/login" className="text-light mx-3 btn btn-primary text-decoration-none">Login</Link>
                            <Link to="/register" className="text-light  btn btn-primary text-decoration-none">Register</Link>

                        </div>

                    </div>
                </div>
            </nav>
        </div>
    )
}