import { useFormik } from 'formik';
import $ from "jquery";
import {useNavigate} from "react-router-dom"
import { useCookies } from 'react-cookie';

export default function SbitRegistration() {
  const navigate = useNavigate()
  const [cookie,setcookie,removecookie] = useCookies(["userId"])
  const formik = useFormik({
    initialValues: {
      firstname: "",
      lastname: "",
      emailid: "",
      password : "",
      contactno: "",
      ModeOfTraining: "",
      course: ""
    },
    onSubmit: values => {
      alert(JSON.stringify(values))
      $.ajax({
        method : "POST",
        url : "http://localhost:8080/registerstudent",
        data : values,
      })
      
      navigate("/login")
       setcookie("userId",values.emailid)

    }
  })
  return (

    <>
      <div >
        <h3>Register for course</h3>
        <form onSubmit={formik.handleSubmit}>

          <label for="exampleInputEmail1" class="form-label" >First Name</label>
          <input name="firstname" value={formik.values.firstname}  onChange={formik.handleChange} type="text" class="form-control" id="exampleInputEmail1" />

          <label for="exampleInputEmail1" class="form-label">Last Name</label>
          <input name="lastname" value={formik.values.lastname}  onChange={formik.handleChange} type="text" class="form-control" id="exampleInputEmail1" />

          <label for="exampleInputEmail1" class="form-label">Email Id</label>
          <input type="text" name="emailid" value={formik.values.emailid}  onChange={formik.handleChange} class="form-control" id="exampleInputEmail1" />

          <label for="exampleInputEmail1" class="form-label">Passwaord</label>
          <input type="text" name="password" value={formik.values.password}  onChange={formik.handleChange} class="form-control" id="exampleInputEmail1" />

          <label for="exampleInputEmail1" class="form-label">Contact no</label>
          <input type="text" name="contactno" value={formik.values.contactno}  onChange={formik.handleChange} class="form-control" id="exampleInputEmail1" />

          <label for="exampleInputEmail1" class="form-label">Mode of Traning</label>
          <select class="form-select" name="ModeOfTraining" value={formik.values.ModeOfTraining}  onChange={formik.handleChange}>
          <option  value="online">Online Traning</option>
            <option value="offline">Offline Traning</option>
            </select>

          <label for="exampleInputEmail1" class="form-label">Courses</label>
          <select class="form-select" name="course" value={formik.values.course}  onChange={formik.handleChange} >
            <option selected>Select course</option>
            <option value="computerBasic">Computer basic</option>
            <option value="java">Java</option>
            <option value="tally">Tally</option>
            <option value="advanceTally">Addvance tally</option>
            <option value="excel">Excel</option>
            <option value="advanceExcel">Addvance excel</option>
            <option value="python">Python</option>
            <option value="java">Java</option>
            <option value="html">Html</option>
            <option value="css">Css</option>
            <option value="webDesigning">Web designing</option>
            <option value="php">Php</option>
            <option value="mysql">MySql</option>
            <option value="springBoot">Springboot</option>
            <option value="reactJs">React JS</option>
            <option value="advanceJava">Addvance java</option>

          </select>

          <button class="btn p-3 fs-5 btn-primary w-100  mt-3" type="submit">Register</button>


        </form>

      </div>
    </>
  )
}