import SbitHeader from "../Components/SbitHeader"
import "../css/SbitHeader.css"
import { Routes, Route, Link } from "react-router-dom"
import SbitFooter from "../Components/SbitFooter"
import SbitHome from "./SbitHome"
import SbitInternship from "./SbitInternship"
import SbitAbout from "./SbitAbout"
import LiveProject from "../Components/LiveProject"
import SbitProject from "../Components/SbitProject"
import SbitService from "./SbitService"
import SbitCourse from "./SbitCourse"
import { UiContent } from "../Components/UiContent"
import SbitStudent from "./SbitStudents"
import SbitLogin from "../Components/SbitLogin"
import SbitRegistration from "../Components/SbitRegistration"
export default function SbitIndex() {
        return (

                <div className="container-fluid main-cont">
                        <SbitHeader />
                        <div>


                                <Routes>
                                        <Route path="/" element={<SbitHome />}>

                                        </Route>
                                        <Route path="/service" element={<SbitService />}>

                                        </Route>
                                        <Route path="/course" element={<SbitCourse />}>

                                        </Route>

                                        <Route path="/student" element={<SbitStudent />}>

                                        </Route>

                                        <Route path="/login" element={<SbitLogin />}>
                                        </Route>
                                        <Route path="/UiCourse" element={<UiContent />}>
                                        </Route>

                                        <Route path="/About" element={<SbitAbout />}>
                                        </Route>

                                        <Route path="/Internship" element={<SbitInternship />}>
                                        </Route>
                                        <Route path="/project" element={<SbitProject />}></Route>
                                        <Route path="/LiveProject" element={<LiveProject />}></Route>
                                        <Route path="/register" element={<div className="w-50 register my-5">
                                                <SbitRegistration />
                                        </div>}>

                                        </Route>
                                </Routes>

                        </div>

                        <SbitFooter />


                </div>

        )
}