import img1 from "../images/py.png" 
import img2 from "../images/c_c++.avif"
import img3 from "../images/java.webp"
import img4 from "../images/sbit_logo.png"
import img5 from "../images/ui_ux.jpg"
import img6 from "../images/internship.jpg"
import img7 from "../images/react.jpg"
import img8 from "../images/training.webp" 


export default function SbitSlider() {
    return (
        <>
            <div class="slide-container">
                <div id="carouselExampleControls" class="carousel slide" data-bs-ride="carousel">
                    <div class="carousel-inner">
                        <div class="carousel-item active">
                            <img src={img1} class="d-block w-100" alt="..." height="100%" width="100%" />
                        </div>
                        <div class="carousel-item">
                            <img src={img2} class="d-block w-100" height="100%" width="100%" alt="..." />
                        </div>
                        <div class="carousel-item">
                            <img src={img3} class="d-block w-100" alt="..." height="100%" width="100%" />
                        </div>
                        <div class="carousel-item">
                            <img src={img4} class="d-block w-100" height="100%" width="100%" alt="..." />
                        </div>
                        <div class="carousel-item">
                            <img src={img5} class="d-block w-100" height="100%" width="100%" alt="..." />
                        </div>
                        <div class="carousel-item">
                            <img src={img6} class="d-block w-100" height="100%" width="100%" alt="..." />
                        </div>
                    </div>
                    <button class="carousel-control-prev" type="button" data-bs-target="#carouselExampleControls" data-bs-slide="prev">
                        <span class="carousel-control-prev-icon" aria-hidden="true"></span>
                        <span class="visually-hidden">Previous</span>
                    </button>
                    <button class="carousel-control-next" type="button" data-bs-target="#carouselExampleControls" data-bs-slide="next">
                        <span class="carousel-control-next-icon" aria-hidden="true"></span>
                        <span class="visually-hidden">Next</span>
                    </button>
                </div>


            </div>
        </>
    )
}