import {useFormik} from "formik"
import { useEffect, useState } from "react"
import $ from "jquery"
import { useNavigate } from "react-router-dom"

export default function SbitLogin() {
    const navigate = useNavigate()
    const [user,setuser] = useState([])
    const formik = useFormik({
        initialValues : {
            emailid : "",
            password : ""
        },
        onSubmit : values =>{
            alert(JSON.stringify(values))
            for(let index of user){
                if(index.emailid == values.emailid && index.password == values.password){
                    navigate("/student");
                    console.log("match")
                    break;
                }
                else{
                    navigate("/register")
                    console.log("does not match")
                }
            }
        }   
     })

     useEffect(()=>{
            $.ajax({
                method : "GET",
                url : "http://localhost:8080/registerstudent",
                success : function(data){
                    setuser(data)
                    console.log(data)
                }
            })
     },[])

    return (
        <>
            <div className="w-25 register mb-5">
                <h1>Student Login </h1>
                <form onSubmit={formik.handleSubmit}>
                    <div class="mb-3">
                        <label for="exampleInputEmail1" class="form-label">Email address</label>
                        <input name="emailid" value={formik.values.emailid} onChange={formik.handleChange} type="email" class="form-control" id="exampleInputEmail1"  />
                        
                    </div>
                    <div class="mb-3">
                        <label for="exampleInputPassword1" class="form-label">Password</label>
                        <input name="password" value={formik.values.password} onChange={formik.handleChange} type="password" class="form-control" id="exampleInputPassword1" />
                    </div>

                    <button type="submit" class="btn btn-primary w-100 fs-4 p-1">Login</button>
                </form>
               
            </div>
        </>
    )
}